import { Button } from 'antd';
import React, { useEffect, useState, useRef } from 'react';
import useStore from '../store';
import { useNavigate } from 'react-router-dom';

const B2 = () => {
  const name = useStore(state => state.name);
  const age = useStore(state => state.age);
  const sex = useStore(state => state.sex);

  const setName = useStore(state => state.setName);
  const setAge = useStore(state => state.setAge);
  const setSex = useStore(state => state.setSex);
  
  const nameInputRef = useRef(null); // refnull
  const ageInputRef = useRef(null);
  const sexInputRef = useRef(null);

  const navigate = useNavigate();

  useEffect(() => {
    // console.log(name);
    // console.log(age);
    // console.log(sex);
  });

  const clearAndFocusInput = (inputRef) => {
    inputRef.current.value = ''; // 
    inputRef.current.focus(); // 
  };

  return (
    <div>
      B2 <br /> <br />
      

      <div>
        <p><input type="text" value={name} onChange={(e) => setName(e.target.value)} ref={nameInputRef} />
        </p>
        <p><input value={age} type="text" onChange={(e) => setAge(e.target.value)} ref={ageInputRef} />
        </p>
        <p><input value={sex} type="text" onChange={(e) => setSex(e.target.value)} ref={sexInputRef} />
        </p>
      </div>

      <Button type="primary" onClick={() => {
        navigate('/c');
        clearAndFocusInput(nameInputRef);
        clearAndFocusInput(ageInputRef);
        clearAndFocusInput(sexInputRef);
      }}></Button>
    </div>
  );
};

export default B2;
